.doc-nav-item {
  display: block;
  height: 30px;
  line-height: 30px;
  cursor: pointer;
}
.doc-nav-item .span-style {
  display: block;
  text-align: center;
  font-size: 16px;
}
.doc-nav-item input {
  display: none;
}
.doc-nav-item input:checked + span {
  color: #333;
  font-size: 16px;
  display: block;
  text-align: center;
}
.doc-nav-item:hover {
  color: lightblue;
}
