header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  border-bottom: 1px solid #eee;
  background: linear-gradient(90deg, #20a0ff 10%, #9dd5ff 90%);
  z-index: 101; }
  header h2 {
    font-size: 32px;
    margin-left: 20px;
    color: #fff;
    font-weight: normal; }
  header img,
  header h2 {
    display: inline-block;
    vertical-align: middle;
    margin: 0; }
  header img {
    width: 110px;
    height: 38px;
    margin-right: 20px; }
  header .header-inner {
    padding: 10px 10px 10px 40px; }
