* {
  padding: 0;
  margin: 0; }

.example-body {
  padding-top: 56px; }
  .example-body .example-aside {
    width: 220px;
    overflow: auto;
    position: fixed;
    height: calc(100% - 55px); }
    .example-body .example-aside::-webkit-scrollbar {
      width: 5px;
      height: 10px; }
    .example-body .example-aside::-webkit-scrollbar-track {
      background-color: #fff; }
    .example-body .example-aside::-webkit-scrollbar-thumb {
      border-radius: 10px;
      background-color: rgba(0, 0, 0, 0.2); }
  .example-body .markdown-body {
    padding: 10px 400px 50px 250px; }
    .example-body .markdown-body h4 {
      margin-top: 30px; }

.example-iphone {
  position: fixed;
  width: 330px;
  height: 725px;
  top: 70px;
  right: 40px;
  z-index: 1; }
  .example-iphone-iframe {
    position: absolute;
    top: 80px;
    left: 21px;
    right: 23px;
    bottom: 104px;
    overflow: hidden;
    border: 1px solid #edf0f4;
    border-top: none;
    background-color: #edf0f4; }

.example-root {
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #666;
  font-size: 14px; }
  .example-root .markdown-body h2 {
    margin: 20px 0; }
  .example-root .markdown-body pre {
    border: 1px solid #eaeefb;
    background-color: #fafafa; }
  .example-root .markdown-body table {
    width: 100%;
    text-align: left; }
    .example-root .markdown-body table td,
    .example-root .markdown-body table th {
      border-bottom: 1px solid #f5f5f5;
      padding: 8px; }
