












































.doc-nav-item {
  display: block;
  height: 30px;
  line-height: 30px;
  cursor: pointer;
  .span-style {
      display: block;
      text-align: center;
      font-size: 16px;
  }
  input {
    display: none;
    &:checked + span {
      color: #333;
      font-size: 16px;
      display: block;
      text-align: center;
    }
  }
  &:hover {
    color: lightblue;
  }
}
